import {
  getTaskList,
  addTask,
  GetTaskDetails,
  UpdateTaskStatus,
  deleteTask,
  GetInspectionPointList,
  GetPointDetails,
  GetAreaByTaskId,
  UpdateTask
} from '@/api/task';
import {parseTime} from '@/utils/index.js';
var state = {
  taskList: [
    {
      id: 1,
      name: '',
      type: '',
      typeStr: '',
      person: '',
      participants: '',
      planTime: '2019-01-01 9:00',
      planEndTime: '2019-01-01 17:00',
      endTime: '2019-01-01 17:00',
      stopTime: '320',
      status: '',
      statusStr: '',
      startTime: '',
      OverdueTime: '',
      SuspendTime: '',
      finishTime: ''
    }
  ],
  taskStatus: '',
  searchText: '',
  currentPage: 1,
  listTotalCount: 1, // 
  pageData: [30, 40, 50, 100], // 

  messageText: '', // 

  editModalVisble: false,
  addModalVisible: false,
  checkModalVisible: false,
  // 
  taskDetail: {
    areaId: '',
    areaName: '',
    closeReason: null,
    endTime: null,
    id: '',
    name: '',
    participantIds: [],
    participants: [],
    pauseTime: null,
    person: '',
    personId: '',
    planEndTime: '',
    planStartTime: '',
    remark: '',
    resourcelist: [],
    startTime: null,
    status: '',
    statusStr: '',
    stopReason: null,
    type: '',
    typeStr: ''
  },
  // 
  taskDetails: {
    areaId: '',
    areaName: '',
    closeReason: null,
    endTime: null,
    id: '',
    name: '',
    participantIds: [],
    participants: [],
    pauseTime: null,
    person: '',
    personId: '',
    planEndTime: '',
    planStartTime: '',
    remark: '',
    resourcelist: [],
    startTime: null,
    status: '',
    statusStr: '',
    stopReason: null,
    type: '',
    typeStr: ''
  },
  // 
  inspectionPointList: [],
  // 
  pointDetails: {},
  // 
  areaDetail: {},

  // 
  loading: true
};

var mutations = {
  set_task_list: function(state, data) {
    state.taskList = data;
  },
  update_modal_status: function(state, modal) {
    state[modal.name] = modal.status;
  },
  // ...
  update_taskStatus: function(state, data) {
    state.taskStatus = data;
    console.log(state.taskStatus);
  },
  // 
  update_search: function(state, data) {
    state.taskName = data.taskName;
    state.startTime = data.startTime;
    state.endTime = data.endTime;
  },
  // 
  set_listTotal: function(state, data) {
    state.listTotalCount = data;
  },
  // 
  set_message: function(state, data) {
    state.messageText = data;
  },
  // 
  set_taskDetail: function(state, data) {
    state.taskDetail = data;
    state.taskDetails = data;
  },
  // 
  set_inspectionPointList: function(state, data) {
    state.inspectionPointList = data;
    console.log(state.inspectionPointList);
  },
  // 
  set_pointDetails: function(state, data) {
    state.pointDetails = data;
    console.log(state.pointDetails);
  },
  // 
  set_areaDetail: function(state, data) {
    state.areaDetail = data;
    console.log(state.areaDetail);
  },
  // 
  set_loading: function(state, data) {
    state.loading = data;
  }
};

var actions = {
  // 
  getTaskList({commit}, param) {
    commit('set_loading', true);
    let data = '';
    console.log(param);
    if (param.status == 0) {
      data = {
        pageIndex: param.currentPage,
        maxResultCount: param.maxResultCount
      };
    } else {
      data = {
        status: param.status,
        pageIndex: param.currentPage,
        maxResultCount: param.maxResultCount
      };
    }
    return new Promise((resolve, reject) => {
      getTaskList(data)
        .then(response => {
          console.log(response);
          if (response.success) {
            if (response.result.items.length != 0) {
              response.result.items.forEach(item => {
                if (item.planStartTime != null || item.planStartTime != '') {
                  item.planStartTime = parseTime(
                    item.planStartTime,
                    '{y}-{m}-{d} {h}:{i}'
                  );
                }
                if (item.planEndTime != null || item.planEndTime != '') {
                  item.planEndTime = parseTime(
                    item.planEndTime,
                    '{y}-{m}-{d} {h}:{i}'
                  );
                }
                if (item.endTime != null || item.endTime != '') {
                  item.endTime = parseTime(item.endTime, '{y}-{m}-{d} {h}:{i}');
                }
              });
            }
            commit('set_task_list', response.result.items);
            commit('set_listTotal', response.result.totalCount);
            // setTimeout(() => {
            //   commit('set_loading', false);
            // }, 3000);
            commit('set_loading', false);
            resolve(response);
          }
        })
        .catch(error => {
          commit('set_message', error.message);
          commit('set_loading', false);
          reject(error);
        });
    });
  },
  // 
  searchTask({commit}, param) {
    console.log(param);
    let data = {
      pageIndex: param.currentPage,
      maxResultCount: param.maxResultCount,
      startTime: param.startTime,
      endTime: param.endTime,
      name: param.taskName
    };
    return new Promise((resolve, reject) => {
      getTaskList(data)
        .then(response => {
          if (response.success) {
            if (response.result.items.length != 0) {
              response.result.items.forEach(item => {
                if (item.planEndTime != null || item.planEndTimeTime != '') {
                  item.planEndTime = parseTime(
                    item.planEndTime,
                    '{y}-{m}-{d} {h}:{i}'
                  );
                }
                if (item.planStartTime != null || item.planStartTime != '') {
                  item.planStartTime = parseTime(
                    item.planStartTime,
                    '{y}-{m}-{d} {h}:{i}'
                  );
                }
                if (item.endTime != null || item.endTime != '') {
                  item.endTime = parseTime(item.endTime, '{y}-{m}-{d} {h}:{i}');
                }
              });
            }
            commit('set_task_list', response.result.items);
            commit('set_listTotal', response.result.totalCount);
            resolve(response);
          }
        })
        .catch(error => {
          commit('set_message', error.message);
          reject(error);
        });
    });
  },
  // 
  addTask({commit}, data) {
    return new Promise((resolve, reject) => {
      addTask(data)
        .then(response => {
          console.log(response);
          if (response.success) {
            commit('set_message', '');
            resolve(response);
          }
        })
        .catch(error => {
          commit('set_message', error.message);
          reject(error);
        });
    });
  },
  // 
  GetTaskDetails({commit}, data) {
    console.log(data);
    return new Promise((resolve, reject) => {
      GetTaskDetails(data)
        .then(response => {
          console.log(response);
          if (response.success) {
            let data = response.result;
            if (data.planStartTime != null || data.planStartTime != '') {
              data.planStartTime = parseTime(
                data.planStartTime,
                '{y}-{m}-{d} {h}:{i}'
              );
            }
            if (data.planEndTime != null || data.planEndTime != '') {
              data.planEndTime = parseTime(
                data.planStartTime,
                '{y}-{m}-{d} {h}:{i}'
              );
            }
            if (data.endTime != null || data.endTime != '') {
              data.endTime = parseTime(data.endTime, '{y}-{m}-{d} {h}:{i}');
            }
            commit('set_taskDetail', data);
            resolve(response);
          }
        })
        .catch(error => {
          commit('set_message', error.message);
          reject(error);
        });
    });
  },
  // 
  UpdateTaskStatus({commit}, data) {
    return new Promise((resolve, reject) => {
      UpdateTaskStatus(data)
        .then(response => {
          console.log(response);
          if (response.success) {
            resolve(response);
          } else {
            commit('set_message', response.error.message);
          }
        })
        .catch(error => {
          commit('set_message', error.message);
          reject(error);
        });
    });
  },
  // 
  deleteTask({commit}, data) {
    return new Promise((resolve, reject) => {
      deleteTask(data)
        .then(response => {
          console.log(response);
          if (response.success) {
            resolve(response);
          } else {
            commit('set_message', response.error.message);
          }
        })
        .catch(error => {
          commit('set_message', error.message);
          reject(error);
        });
    });
  },
  // 
  GetInspectionPointList({commit}, data) {
    return new Promise((resolve, reject) => {
      GetInspectionPointList(data)
        .then(response => {
          console.log(response);
          if (response.success) {
            commit('set_inspectionPointList', response.result.items);
            resolve(response);
          }
        })
        .catch(error => {
          commit('set_message', error.message);
          reject(error);
        });
    });
  },
  // 
  GetPointDetails({commit}, data) {
    return new Promise((resolve, reject) => {
      GetPointDetails(data)
        .then(response => {
          console.log(response);
          if (response.success) {
            if (response.result != null) {
              commit('set_pointDetails', response.result);
            }
            resolve(response);
          } else {
            commit('set_message', response.error.message);
          }
        })
        .catch(error => {
          commit('set_message', error.message);
          reject(error);
        });
    });
  },
  // 
  GetAreaByTaskId({commit}, data) {
    return new Promise((resolve, reject) => {
      GetAreaByTaskId(data)
        .then(response => {
          console.log(response);
          if (response.success) {
            commit('set_areaDetail', response.result);
            resolve(response);
          } else {
            commit('set_message', response.error.message);
          }
        })
        .catch(error => {
          commit('set_message', error.message);
          reject(error);
        });
    });
  },
  UpdateTask({commit}, data) {
    return new Promise((resolve, reject) => {
      UpdateTask(data)
        .then(response => {
          console.log(response);
          if (response.success) {
            commit('set_message', '');
            resolve(response);
          } else {
            console.log(response);
            commit('set_message', response.error.message);
          }
        })
        .catch(error => {
          commit('set_message', error.message);
          reject(error);
        });
    });
  },
  // 
  setMessage({commit}, data) {
    commit('set_message', data);
  }
};

export default {
  namespaced: true,
  state,
  mutations,
  actions
};
